import React, {Component} from "react";
import {connect} from "react-redux";
import OraModal from "eshop/modules/core/components/ui/Modal";
import { getShowLogoutConfirmationModal} from "eshop/modules/auth/selectors/authorization";
import {doLogOut}    from "eshop/modules/auth/actions/api"
import {hideLogoutConfirmationModal} from "eshop/modules/auth/actions/authorization";
class LogoutConfirmationModal extends React.Component{
    onSubmit(event){
        event.preventDefault();
        this.props.doLogOut();
     }
     onClose(){
        this.props.hideLogoutConfirmationModal();
     }

    getModalProps() {
        return {
            size: "narrow",
            escapeClose: false,
            clickClose: false
        };
    }
    render(){
    return (
        <OraModal  {...this.getModalProps()} showClose={true} open={this.props.showLogoutConfirmationModal} onClose={this.onClose.bind(this)} id="logout-confirmation-modal">
            <div className="l-row">
                <div className="l-col l-col-12">
                    <h2>{this.props.descriptions.logoutConfirmationTitle||"Uwaga"}</h2>
                </div>
            </div>
            <div className="l-row">
                <div className="l-col l-col-12">
                    {this.props.descriptions.logoutConfirmationBody||"Czy na pewno chcesz się wylogować? To spowoduje usunięcie wszystkich produktów i usług z koszyka."}
                </div>
            </div>
            <form id="msisdnForm" onSubmit={this.onSubmit.bind(this)}>
            <div className="l-row ">
                <div className="l-col l-col-12 ">
                    <div className="o-separator o-separator--s u-spacing-top-l"></div>
                </div>
            </div>
            <div className="l-row ">
                <div className="l-col l-col-4 l-col-small-12">
                    </div>
                    <div className="l-col l-col-4 l-col-small-12 u-padding-top-l">

                    <button id="doLogout" type="submit" className="o-btn opl-btn u-w-100"><span className="opl-ripple-box"><span className="opl-ripple-circle"></span></span>Wyloguj</button>
                    </div>

                    <div className="l-col l-col-4 l-col-small-12 u-padding-top-l">
                        <button id="cancelLogout" type="button"  onClick={this.onClose.bind(this)} className="o-btn opl-btn opl-btn--primary u-w-100">Anuluj</button>
                    </div>
            </div>
            </form>
        </OraModal>)
    }



}

const mapStateToProps = state => ({
    showLogoutConfirmationModal:getShowLogoutConfirmationModal(state)
});

const mapDispatchToProps = dispatch => ({
    hideLogoutConfirmationModal:()=>dispatch(hideLogoutConfirmationModal()),
    doLogOut:() =>dispatch(doLogOut())
});

export default connect(mapStateToProps, mapDispatchToProps)(LogoutConfirmationModal);



